'use client'

import { useState, useMemo } from 'react'
import { Institution } from '@/lib/data-context'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { maskCNPJ, maskPhone } from '@/lib/utils'
import { Edit, Trash2, MoreVertical, Search } from 'lucide-react'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'

interface InstitutionListProps {
  institutions: Institution[]
  onEdit: (institution: Institution) => void
  onDelete: (id: string) => void
}

export function InstitutionList({ institutions, onEdit, onDelete }: InstitutionListProps) {
  const [searchTerm, setSearchTerm] = useState('')

  const getTypeLabel = (type: string) => {
    const types: Record<string, string> = {
      school: 'Escola',
      center: 'Centro Educacional',
      other: 'Outro'
    }
    return types[type] || type
  }

  const filteredInstitutions = useMemo(() => {
    if (searchTerm.trim() === '') {
      return institutions
    }
    
    const searchLower = searchTerm.toLowerCase().trim()
    const searchNumbers = searchTerm.replace(/\D/g, '')
    
    return institutions.filter(institution => {
      const cnpjMasked = maskCNPJ(institution.cnpj)
      const phoneMasked = institution.phone ? maskPhone(institution.phone) : ''
      const typeLabel = getTypeLabel(institution.type).toLowerCase()
      
      return (
        institution.name.toLowerCase().includes(searchLower) ||
        institution.cnpj.includes(searchNumbers) ||
        cnpjMasked.toLowerCase().includes(searchTerm.toLowerCase()) ||
        institution.city.toLowerCase().includes(searchLower) ||
        institution.principalName.toLowerCase().includes(searchLower) ||
        typeLabel.includes(searchLower) ||
        (institution.phone && institution.phone.includes(searchNumbers)) ||
        phoneMasked.toLowerCase().includes(searchTerm.toLowerCase())
      )
    })
  }, [institutions, searchTerm])

  if (institutions.length === 0) {
    return (
      <Card className="p-12 text-center">
        <p className="text-muted-foreground">Nenhuma instituição cadastrada ainda</p>
      </Card>
    )
  }

  return (
    <div className="space-y-4">
      {/* Barra de Pesquisa */}
      <div className="relative">
        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
        <Input
          type="text"
          placeholder="Pesquisar por nome, CNPJ, cidade, diretor, tipo ou telefone..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="pl-10"
        />
      </div>

      {filteredInstitutions.length === 0 && searchTerm.trim() !== '' ? (
        <Card className="p-12 text-center">
          <p className="text-muted-foreground">Nenhuma instituição encontrada com o termo "{searchTerm}"</p>
        </Card>
      ) : (
        <>
          {filteredInstitutions.map(institution => (
        <Card key={institution.id} className="p-4">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
            <div className="flex-1">
              <h3 className="font-semibold text-lg">{institution.name}</h3>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-2 mt-2 text-sm text-muted-foreground">
                <div><strong>CNPJ:</strong> {maskCNPJ(institution.cnpj)}</div>
                <div><strong>Tipo:</strong> {getTypeLabel(institution.type)}</div>
                <div><strong>Diretor:</strong> {institution.principalName}</div>
                <div><strong>Telefone:</strong> {institution.phone ? maskPhone(institution.phone) : '-'}</div>
              </div>
              <div className="mt-2 flex items-center gap-2">
                <span className={`px-2 py-1 text-xs rounded-full ${
                  institution.status === 'active'
                    ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
                    : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
                }`}>
                  {institution.status === 'active' ? 'Ativo' : 'Inativo'}
                </span>
              </div>
            </div>
            <div className="flex justify-end">
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
                    <MoreVertical className="h-4 w-4" />
                    <span className="sr-only">Abrir menu</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem onClick={() => onEdit(institution)}>
                    <Edit className="h-4 w-4 mr-2" />
                    Editar
                  </DropdownMenuItem>
                  <DropdownMenuItem
                    onClick={() => onDelete(institution.id)}
                    variant="destructive"
                  >
                    <Trash2 className="h-4 w-4 mr-2" />
                    Excluir
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
          </div>
        </Card>
      ))}
      {filteredInstitutions.length > 0 && (
        <div className="text-sm text-muted-foreground">
          Mostrando {filteredInstitutions.length} de {institutions.length} instituição(ões)
        </div>
      )}
        </>
      )}
    </div>
  )
}
